@import url(https://fonts.googleapis.com/css2?family=Lato:wght@300&display=swap);
html{font-size:62.5%}body{margin:0;font-family:"Lato", sans-serif}.page__wrap{display:flex;flex-flow:column nowrap;justify-content:center;align-items:center;width:100vw;height:100vh;background-image:url(/form/static/media/background.1073a049.jpg);background-size:cover}.myForm{width:40%;min-width:31rem;height:80%;min-height:36rem;display:flex;flex-flow:column nowrap;justify-content:space-evenly;align-items:center;padding:3rem 1rem 1rem 1rem;box-sizing:border-box;border:2px solid #fcfcfc;border-radius:3rem;background:rgba(255,255,255,0.05)}.myForm__wrap{display:flex;flex-flow:column nowrap;justify-content:center;align-items:center;width:100%;height:80%}.input,.input__text,.input__time,.input__select{display:flex;flex-flow:row nowrap;justify-content:center;align-items:center;width:80%;height:3rem;margin:1rem 1rem;padding-left:1rem;box-sizing:border-box;border:1px solid white;border-radius:1rem;outline:none;color:#00004d}.input__text::-webkit-input-placeholder{color:#00004d}.input__text:-ms-input-placeholder{color:#00004d}.input__text::placeholder{color:#00004d}.input__time{min-height:2rem}.input__submit{width:40%;height:10%;border-radius:20px;background-color:transparent;transition:0.2s all linear;border:1px solid #fcfcfc;outline:none;font-size:2rem;font-weight:400;color:#00004d;background-color:#fcfcfc}.input__submit:hover{cursor:pointer;-webkit-transform:scale(1.1);transform:scale(1.1);background-color:rgba(0,128,0,0.7);border:none}.input__error-message{position:relative;bottom:1rem;font-size:2rem;font-weight:600;text-align:center;color:red}@media only screen and (max-width: 768px){.input__submit{width:50%}}@media only screen and (max-height: 520px){.myForm{height:90%;min-height:25rem}.input__error-message{font-size:1.5rem}}.greetings{display:flex;flex-flow:column nowrap;justify-content:center;align-items:center;width:100%;height:20%;padding:1rem 2rem;box-sizing:border-box;font-size:4rem;font-weight:600;color:white;text-align:center;-webkit-animation:greetingsAppears 4s;animation:greetingsAppears 4s}@-webkit-keyframes greetingsAppears{0%{color:transparent}100%{color:white}}@keyframes greetingsAppears{0%{color:transparent}100%{color:white}}@media only screen and (max-width: 768px){.greetings{font-size:3rem}}@media only screen and (max-width: 480px){.greetings{font-size:2rem}}

