.myForm {
  width: 40%;
  min-width: 31rem;
  height: 80%;
  min-height: 36rem;
  @include setFlex(column, space-evenly, center);
  padding: 3rem 1rem 1rem 1rem;
  box-sizing: border-box;
  border: 2px solid $c-border;
  border-radius: 3rem;
  background: rgba(255, 255, 255, 0.05);

  &__wrap {
    @include setFlex(column, center, center);
    width: 100%;
    height: 80%;
  }
}

.input {
  @include setFlex(row, center, center);
  width: 80%;
  height: 3rem;
  margin: 1rem 1rem;
  padding-left: 1rem;
  box-sizing: border-box;
  border: 1px solid white;
  border-radius: 1rem;
  outline: none;
  color: $c-input-text;

  &__text {
    @extend .input;

    &::placeholder {
      color: $c-input-text;
    }
  }

  &__time {
    @extend .input;
    min-height: 2rem;
  }

  &__select {
    @extend .input;
  }

  &__submit {
    width: 40%;
    height: 10%;
    border-radius: 20px;
    background-color: transparent;
    transition: 0.2s all linear;
    border: 1px solid $c-border;
    outline: none;
    font-size: 2rem;
    font-weight: 400;
    color: $c-input-text;
    background-color: $c-border;

    &:hover {
      cursor: pointer;
      transform: scale(1.1);
      background-color: rgba(green, .7);
      border:none;
    }
  }

  &__error-message {
    position: relative;
    bottom: 1rem;
    font-size: 2rem;
    font-weight: 600;
    text-align: center;
    color: $c-error;
  }
}

@media only screen and (max-width: 768px) {
  .input__submit {
    width: 50%;
  }
}

@media only screen and (max-height: 520px) {
  .myForm {
    height: 90%;
    min-height: 25rem;
  }

  .input {
    &__error-message {
      font-size: 1.5rem;
    }
  }
}
